.l-topbar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  text-align: center;
  height: 70px;
  display: block;
  background: #56B784;
  border-bottom: 1px solid #49ab77;
  z-index: 10; }

.l-container {
  margin: 0 auto;
  padding: 0 3em;
  max-width: 1100px; }

.l-columns {
  width: 100%;
  display: block; }
  .l-columns::after {
    clear: both;
    content: "";
    display: table; }

.l-columns--thirds .l-col {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 31.76157%; }
  .l-columns--thirds .l-col:last-child {
    margin-right: 0; }

.l-columns--split .l-col {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 48.82117%; }
  .l-columns--split .l-col:last-child {
    margin-right: 0; }

.l-col {
  position: relative;
  vertical-align: top; }

.l-columns--sideright .l-col {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 65.88078%; }
  .l-columns--sideright .l-col:last-child {
    margin-right: 0; }
.l-columns--sideright .l-col--side {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 31.76157%; }
  .l-columns--sideright .l-col--side:last-child {
    margin-right: 0; }

.l-columns--sideleft .l-col {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 65.88078%;
  margin-left: 34.11922%; }
  .l-columns--sideleft .l-col:last-child {
    margin-right: 0; }
.l-columns--sideleft .l-col--side {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 31.76157%;
  margin-left: -102.35765%; }
  .l-columns--sideleft .l-col--side:last-child {
    margin-right: 0; }

@media screen and (min-width: 40em) and (max-width: 63.99em)  {
  .l-page {
    padding: 2em 0; }
  .l-container {
    padding: 0 2em; } }

@media screen and (max-width: 39.99em)  {
  .l-topbar {
    left: 0; }
  .l-nav {
    z-index: 5; }
  .l-page {
    padding: 1em 0;
    margin-left: 0;
    z-index: 5; }
  .l-container {
    padding: 0 1em; }
  .l-col {
    margin-bottom: 2em; }
    .l-col:last-child {
      margin-bottom: 0; }
  .l-columns--thirds .l-col {
    float: left;
    display: block;
    margin-right: 7.42297%;
    width: 100%; }
    .l-columns--thirds .l-col:last-child {
      margin-right: 0; }
  .l-columns--split .l-col {
    float: left;
    display: block;
    margin-right: 7.42297%;
    width: 100%; }
    .l-columns--split .l-col:last-child {
      margin-right: 0; } }

